import { NavLink } from "react-router-dom";
import { HashLink as Link } from "react-router-hash-link";

const navs = {
  home: {
    path: "/",
    name: "",
  },
  aboutus: {
    path: "/aboutus",
    name: "",
  },
  solution: {
    path: "/solution",
    name: "",
  },
  product: {
    path: "/product",
    name: "",
  },
  development: {
    path: "/development",
    name: "",
  },
  performance: {
    path: "/performance",
    name: "",
  },
  contact: {
    path: "/contact",
    name: "",
  },
};

const Navbar = () => {
  return (
    <div className="navbar">
      <NavLink to={navs.home.path} className="navbar-brand">
        <img
          className="navbar-logo"
          src={process.env.PUBLIC_URL + "/img/logo.png"}
          alt=",ETICA BATTERY,Etica Battery,ETICA,etica battery."
        />
      </NavLink>
      <div className="navbar-line"></div>

      <div className="dropMenu">
        <label htmlFor="menu-control" className="menu-btn">
          <span></span>
        </label>
      </div>
      <input type="checkbox" id="menu-control" />

      <ul className="navbar-option">
        <li>
          <NavLink to={navs.home.path}>{navs.home.name}</NavLink>
        </li>
        <li>
          <NavLink to={navs.aboutus.path}>{navs.aboutus.name}</NavLink>
          <div className="dropdown">
            <ul className="subNavbar">
              <li>
                <Link to="/aboutus#introduction"></Link>
              </li>
              <li>
                <Link to="/aboutus#history"></Link>
              </li>
              <li>
                <Link to="/aboutus#business"></Link>
              </li>
              <li>
                <Link to="/aboutus#feature"></Link>
              </li>
            </ul>
          </div>
        </li>
        <li>
          <NavLink to={navs.solution.path}>{navs.solution.name}</NavLink>
          <div className="dropdown">
            <ul className="subNavbar">
              <li>
                <Link to="/solution#commercialFacility"></Link>
              </li>
              <li>
                <Link to="/solution#trsportationApplication"></Link>
              </li>
              <li>
                <Link to="/solution#publicHouse"></Link>
              </li>
              <li>
                <Link to="/solution#industryApplication"></Link>
              </li>
              <li>
                <Link to="/solution#greenEnergyIndustry"></Link>
              </li>
            </ul>
          </div>
        </li>
        <li>
          <NavLink to={navs.product.path}>{navs.product.name}</NavLink>
        </li>
        <li>
          <NavLink to={navs.development.path}>{navs.development.name}</NavLink>
        </li>
        <li>
          <NavLink to={navs.performance.path}>{navs.performance.name}</NavLink>
        </li>
        <li>
          <NavLink to={navs.contact.path}>{navs.contact.name}</NavLink>
        </li>
      </ul>
    </div>
  );
};

export default Navbar;
