import React, { useRef } from "react";
import GoogleMapReact from "google-map-react";
import mapKey from "../Components/key";
import emailjs from "@emailjs/browser";

const Contact = () => {
  // useEffect(() => {
  //   const handleEnter = (e) => {
  //     if (e.key === "Enter") {
  //       e.preventDefault();

  //       console.log("pressed Enter");
  //     }
  //   };

  //   document.addEventListener("keydown", handleEnter);

  //   return () => {
  //     document.addEventListener("keydown", handleEnter);
  //   };
  // }, []);

  // function validateForm() {
  //   let x =
  //     document.forms["contact-form"]["fullname"]["company"]["email"]["msg"]
  //       .value;
  //   if (x === "") {
  //     alert("!");
  //     return false;
  //   }
  // }

  // email
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm(
        "test123456",
        "template_112nvyd",
        form.current,
        "sCK8COnh4d-o6DHKh"
      )
      .then(
        () => {
          alert("");
        },
        () => {
          alert("");
        }
      );
    form.current.reset();
  };

  // google
  const CustomMarker = ({ text }) => (
    <div className="markPoint">
      <div className="point">
        <i className="fa fa-map-marker" aria-hidden="true"></i>
      </div>
      <div className="text">{text}</div>
    </div>
  );

  return (
    <div className="container" style={{ height: "100vh" }}>
      <div className="map">
        <GoogleMapReact
          bootstrapURLKeys={{ key: mapKey }}
          defaultCenter={{ lat: 24.998752, lng: 121.4247906 }}
          defaultZoom={12}
          yesIWantToUseGoogleMapApiInternals
        >
          <CustomMarker
            lat={24.9553234}
            lng={121.3841572}
            text=""
          />
          <CustomMarker lat={25.0480859} lng={121.469599} text="" />
        </GoogleMapReact>
      </div>

      {/*  */}
      <form ref={form} onSubmit={sendEmail} className="contact-form">
        <div className="form-wrap">
          <label></label>
          <input
            type="text"
            name="user_name"
            placeholder=""
          />
          <br />
        </div>
        <div className="form-wrap">
          <label></label>
          <input
            type="text"
            name="company"
            placeholder=""
          />
          <br />
        </div>
        <div className="form-wrap">
          <label></label>
          <input type="email" name="email" placeholder="" />
          <br />
        </div>
        <div className="form-wrap">
          <label></label>
          <textarea name="message" cols="30" rows="10" />
        </div>
        <div className="form-wrap">
          <input type="submit" value="" className="submit" />
        </div>
      </form>
    </div>
  );
};

export default Contact;
