const Performance = () => {
  const data = {
    development: {
      title: "",
      image: process.env.PUBLIC_URL + "/img/05.jpg",
      alt: "",
    },
  };

  return (
    <div className="container">
      <div className="banner">
        <img
          src={data.development.image}
          alt={data.development.alt}
          style={{ width: "100%" }}
        />
        <div className="slash">
          <h2>{data.development.title}</h2>
        </div>
      </div>
    </div>
  );
};

export default Performance;
