import { imgHandler } from "../Components/imgHandler";

const Product4 = () => {
  const data = {
    product_img: {
      image: "/img/product.png",
      alt: "ESS Energy Storage System",
    },
    product4_content: {
      AS1P: {
        img: "/img/1S1P.jpg",
        alt: "1S1P",
      },
    },
  };

  return (
    <div className="container">
      <div className="banner">
        <img
          src={imgHandler(data.product_img.image)}
          alt={data.product_img.alt}
        />
        <div className="slash">
          <h2>LEV</h2>
        </div>
      </div>

      <div className="product4-content">
        <div className="contentWrap">
          <img
            src={imgHandler(data.product4_content.AS1P.img)}
            alt={data.product4_content.AS1P.alt}
          />

          <div className="subtitle">
            <h2 id="1S1P">1S1P</h2>
            <ul>
              <li></li>
              <li></li>
            </ul>
            <br />
          </div>
        </div>

        <table>
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
            <tr>
              <td>3.8 V</td>
              <td>3.8~2.8 V</td>
              <td>1.6 A</td>
              <td>3 A</td>
              <td>1.6 A</td>
              <td>0~45 C</td>
              <td>-20~60 C</td>
              <td>I2C</td>
              <td>89*69.3*28.9 mm</td>
              <td>67 g</td>
            </tr>
          </thead>
        </table>
      </div>
    </div>
  );
};

export default Product4;
